import React, { useState } from "react";
// import promosData from "../db/promotions.json";
// import moment from "moment";
import Product from "./Product";
import NewArrCreate from "../CreateNewArr/NewArrCreate";
import Test from "./test";
import PromoCard from "../PromoCard/PromoCard";
import style from "./Product.module.css";
import CommentsCard from "../PromoCard/CommentsCard/CommentsCard"

const ListOfProducts = () => {


  const [cardsArr, setCardArr] = useState(NewArrCreate);
  const [isCartOpen, setIsCartOpen] = useState(false);
  const [cardPromo, setCardPromo] = useState({});
  const [sort, setSort] = useState("month");
  const [comments, setComments] = useState([])

const commentsItem = {
   author: "",
   comment: "",
   time: Date.now()
}

const addComment =(info) => {
  setComments([...comments, info])
}

const inputHandler = (e) => {
  const name = e.target.name;
  const value =
    e.target.type === "checkbox" ? e.target.checked : e.target.value;

  this.setState({
    [name]: value,
  });
};

  const toggleCart = () => {
    setIsCartOpen(!isCartOpen);
  };

  const addPromo = (id) => {
    const item = cardsArr.find((el) => el.id === id);
    setCardPromo(item, cardPromo);
  };

  const sortType = (e) => {
    e.preventDefault();
    const type = e.target.dataset.sort;
    setSort(type);

  };



  const sortData = () => {

    switch (sort) {
      case "month":
        return cardsArr.sort((a, b) => b.month - a.month);
      case "start":
        return cardsArr.sort((a, b) => a.promoStart - b.promoStart);
      case "end":
        return cardsArr.sort((a, b) => a.promoEnd - b.promoEnd);
      case "chain":
        return cardsArr.sort((a, b) => a.chain - b.chain);
      case "mark":
        return cardsArr.sort((a, b) => a.tradeMark - b.tradeMark);
      case "status":
        return cardsArr.sort((a, b) => a.tradeMark - b.tradeMark);
    }
  };

  return (
    <div>
      <p>HELLOOO</p>
      <p>HEYYYYY</p>
      <PromoCard
        cardPromo={cardPromo}
        isCartOpen={isCartOpen}
        toggleCart={toggleCart}
      />
      {/* <CommentsCard /> */}
      <table>
        <thead className={style.head}>
          <tr className={style.table}>
            <th className={style.item} data-sort="month" onClick={sortType}>
              Місяць
            </th>
            <th className={style.item} data-sort="start" onClick={sortType}>
              Початок акції
            </th>
            <th className={style.item} data-sort="end" onClick={sortType}>
              Кінець акції
            </th>
            <th className={style.itemChain} data-sort="chain" onClick={sortType}>
              Мережа
            </th>
            <th className={style.item} data-sort="mark" onClick={sortType}>
              Торгова марка
            </th>
            <th className={style.item} data-sort="status" onClick={sortType}>
              Статус
            </th>

            <th className={style.item}>Більше</th>
          </tr>
        </thead>

        <tbody className={style.bodytable}>
          {sortData().map((el) => (
            <Product
              el={el}
              key={el.id}
              addPromo={addPromo}
              toggleCart={toggleCart}
              isCartOpen={isCartOpen}
            />
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default ListOfProducts;
