import React from 'react';
import style from '../PromoCard.module.css'

const CalculationItem = ({el, discount}) => {
    console.log(el);
    return (
        <tr >
        <td className={style.calc__point}>{el.sku}</td>
        <td className={style.calc__point}>{el.priceOrdinar}</td>
        <td className={style.calc__point}>{discount} %</td>
        <td className={style.calc__point}>{(el.priceOrdinar-(el.priceOrdinar*discount/100)).toFixed(2)}</td>
        <td className={style.calc__point}><input></input></td>
        <td className={style.calc__point}>рентабельность</td>
        <td className={style.calc__point}>{el.planOfCompensation}</td>
        <td className={style.calc__point}>% переменных</td>
        <td className={style.calc__point}>Авторизация</td>
        <td className={style.calc__point}>Скидка</td>
        <td className={style.calc__point}></td>
        <td className={style.calc__point}></td>
        </tr>
    );
};

export default CalculationItem;