import React from 'react';
import CalculationItem from './CalculationItem'
import style from '../PromoCard.module.css'

const Calculation = ({cardPromo, discount}) => {
    console.log(cardPromo);
      return (
        <table className={style.calc__item}>
        <thead className={style.head}>
        <tr >
        <td className={style.calc__point}>наименование</td>
        <td className={style.calc__point}>цена обычная</td>
        <td className={style.calc__point}>скидка</td>
        <td className={style.calc__point}>цена со скидкой</td>
        <td className={style.calc__point}>себестоимость</td>
        <td className={style.calc__point}>рентабельность</td>
        <td className={style.calc__point}>Количество на акцию</td>
        <td className={style.calc__point}>% переменных</td>
        <td className={style.calc__point}>Авторизация</td>
        <td className={style.calc__point}>Скидка</td>
        <td className={style.calc__point}></td>
        <td className={style.calc__point}></td>
        </tr>
        </thead>
        <tbody>
            {cardPromo.detales.map(el => <CalculationItem el={el} discount={discount}/>)}
            

        </tbody>
        </table>
    );
};

export default Calculation;