import React from 'react';
import style from "./PromoCard.module.css";

const CheckBlock = () => {
    return (
        <div className={style.chekList}>
        <form className={style.checkList__form}>
        <input type="checkbox" className={style.checkList__checkbox}></input>
        <label className={style.checkList__label}>Бренд Менеджер</label>
        </form>
        <form className={style.checkList__form}>
        <input type="checkbox" className={style.checkList__checkbox}></input>
        <label className={style.checkList__label}>Менеджер продажів</label>
        </form>
        <form className={style.checkList__form}>
        <input type="checkbox" className={style.checkList__checkbox}></input>
        <label className={style.checkList__label}>Керівник відділу продажів</label>
        </form>
        <form className={style.checkList__form}>
        <input type="checkbox" className={style.checkList__checkbox}></input>
        <label className={style.checkList__label}>Бренд Менеджер</label>
        </form>

      </div>
    );
};

export default CheckBlock;